/**
 * ------------------------------------------------------------------------
 * Install Command
 * ------------------------------------------------------------------------
 */
import path from 'path';
import { cli, log } from '@bb-cli/base';
import { name as packageName } from '../../package.json';

// setting log heading
log.heading = `[${packageName}]`;
/**
 * Command definition
 * Setting up options and arguments
 */
cli
  .option('-f, --force', 'if the generator is already installed it be overwritten', false)
  .option('-r, --registry', 'install it from a specific registry')
  .man(path.resolve(__dirname, '../../man/bb-generate-install.md'))
  .parse(process.argv);

const items = cli.args || 'Default';

log.info('install', `Installing ${items}`);
